<template>
  <div class="goods">

    <m-header>
      <router-link slot="goods" tag="div" class="home" to="/">
        <i class="home-button iconfont icon-home"></i>
      </router-link>
      <input slot="goods" type="text" placeholder="美图M8" class="search-box">
      <div slot="goods" class="search-btn">
        <router-link to="/">
          搜索
        </router-link>
      </div>
    </m-header>

    <div class="category-wrapper border-tb-1px" v-if="categoryData.length">
      <category>
        <ul class="category-list">
          <li class="category-title">分类：</li>
          <li class="category-all">
            <router-link :to="{name:'lists',params:{id:0}}">全部</router-link>
          </li>
          <li v-for="item in categoryData" :key="item.id">
            <router-link :to="{name:'lists',params:{id:item.id}}">{{ item.name }}</router-link>
          </li>
        </ul>
      </category>
    </div>

    <div class="model-wrapper border-tb-1px" v-show="modelData[category]">
      <ul class="model-list">
        <li class="model-title">机型：</li>
        <li class="model-all">
          <router-link :to="{name:'models',params:{model:0}}">全部</router-link>
        </li>
        <li v-for="item in modelData[category]" :key="item.id" class="model-item">
          <router-link :to="{name:'models',params:{model:item.id}}">{{item.name}}</router-link>
        </li>
      </ul>
    </div>

    <div class="erro-data" v-show="isShow">
      <div class="erro-image"></div>
      <p class="erro-text">没有找到相关的商品</p>
    </div>

    <div class="list-wrap" ref="listWrap">
      <router-view :GoodsListImagesData="GoodsListImages" @sendLoading="getLoading"></router-view>
      <div class="loading-more" v-show="loadingShow">
        <span class="loading-image"></span>
        <span class="loading-text">正在加载中</span>
      </div>
      <p class="loaded-text" v-show="loadedText">没有了～～</p>
    </div>

  </div>
</template>

<script>
import MHeader from 'base/m-header/m-header'
import Category from 'components/goods/category'
import { getGoodsTitleData, getGoodsListData } from 'api'

export default {
  name: 'goods',
  data() {
    return {
      categoryData: [],
      modelData: {},
      category: 0,
      adaptation: 0,
      page: 1,
      totalPage: 0,
      categoryId: [],
      GoodsListImages: [],
      isShow: false,
      loadingShow: false,
      loadedText: false,
      listWrapScrollTop: 0
    }
  },
  created() {
    this._getGoodsTitleData()
  },
  mounted() {
    this.$refs.listWrap.addEventListener('scroll', this.listWrapScroll)
  },
  methods: {
    _getGoodsTitleData() {
      this.$store.dispatch('waitingStart', true)
      getGoodsTitleData().then((res) => {
        this.categoryData = res.list
        res.list.forEach((e, i) => {
          if (e.adaptation_phones.length) {
            this.categoryId.push(e.id)
            this.modelData[e.id] = e.adaptation_phones
          }
        })
        this.category = this.$route.params.id
        if (this.$route.params.model) {
          this.adaptation = this.$route.params.model
        } else {
          this.adaptation = 0
        }
        if(typeof this.category === 'undefined') return
        this._getGoodsListData(this.category, this.adaptation, this.page)
      })
    },
    _getGoodsListData(category, adaptation, page) {
      if (this.page > this.totalPage && this.totalPage) {
        this.loadingShow = false
        this.loadedText = true
        this.$el.nextElementSibling.nextElementSibling.style.display = 'block'
        setTimeout(() => {
          this.listWrapScrollTop = this.$refs.listWrap.scrollHeight - this.$refs.listWrap.offsetHeight
        }, 0)
        return
      }
      getGoodsListData(category, adaptation, page).then((res) => {
        this.$store.dispatch('waitingEnd', false)
        this.page++
        this.totalPage = res.result.total_page
        res.result.list.forEach((e) => {
          this.GoodsListImages.push(e)
        })
        if (this.GoodsListImages.length === 0) {
          this.isShow = true
        }
        console.log(this.GoodsListImages.length);
        this.loadingShow = false
        this.setListWrapHeight()
      })
    },
    setListWrapHeight() {
      let re = /\d+/g
      let arr = window.location.hash.match(re)
      if(!arr) return
      if (arr.length === 1 && this.GoodsListImages.length > 4) {
        this.$refs.listWrap.style.height = window.innerHeight - 94 + 'px'
      }
      if (arr.length === 2 && this.GoodsListImages.length > 4) {
        this.$refs.listWrap.style.height = window.innerHeight - 138 + 'px'
      }
    },
    getLoading(data) {
      if (this.totalPage === 0) { // 防止点击切换执行
        this.$el.nextElementSibling.nextElementSibling.style.display = 'block'
        return
      }
      if (data) {
        this.loadingShow = data
        this._getGoodsListData(this.category, this.adaptation, this.page)
      }
    },
    listWrapScroll() {
      if (this.listWrapScrollTop) {
        let difference = this.listWrapScrollTop - this.$refs.listWrap.scrollTop
        if (difference) {
          window.scrollTo(0, -40)
        }
      }
    }
  },
  watch: {
    '$route'(to, from) {
      let re = /\d+/g
      let arr = window.location.hash.match(re)
      if (this.categoryId.includes(arr[0]) && arr.length === 1) {
        this.$router.push({ name: 'models', params: { model: 0 } }) // 命名式路由
        return // 防止二次请求
      }
      this.category = this.$route.params.id
      if (this.$route.params.model || this.$route.params.model === 0) {
        this.adaptation = this.$route.params.model
      } else {
        this.adaptation = 0
      }
      this.page = 1
      this.GoodsListImages = []
      this.isShow = false
      this.loadedText = false
      this.totalPage = 0
      this.listWrapScrollTop = 0
      this.$refs.listWrap.removeAttribute('style')
      this.$store.dispatch('waitingStart', true)
      this._getGoodsListData(this.category, this.adaptation, this.page)
    }
  },
  components: {
    MHeader,
    Category
  }
}
</script>

<style lang="stylus" scoped>
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .goods
    .m-header
      display: flex
      align-items: center
      .home
        width: 60px
        height: 50px
        line-height: 50px
        text-align: center
        .icon-home
          font-size: $font-size-large-x
      .search-box
        flex 1
        height: 38px
        box-sizing: border-box
        border: 1px solid #cdced1
        text-indent: 10px
        border-radius 4px
      .search-btn
        width: 60px
        height: 50px
        line-height: 50px
        text-align: center

    .category-wrapper
      background: #fff
      border-top-1px(#f6f4f8)

    .model-wrapper
      background: #fff
      border-bottom-1px(#f6f4f8)
      .model-list
        padding: 10px 0
        overflow: hidden
        .model-title
          float: left
          padding: 4px 0 4px 10px
          color: #444
        .model-all
          float: left
          margin-right: 2px
          a
            display: block
            padding: 4px
            color: #444
            &.router-link-active
              background: #f55669
              color: #fff
              border-radius 4px
        .model-item
          float: left
          margin-right: 2px
          a
            display: block
            padding: 4px
            color: #444
            &.router-link-active
              background: #f55669
              color: #fff
              border-radius 4px
          &:last-child
            margin-right: 0

    .erro-data
      padding: 50px 0
      background: #fff
      .erro-image
        width: 140px
        height: 105px
        margin: 0 auto 10px
        bg-image('pic_error')
        background-size: 140px 105px
        background-repeat: no-repeat
      .erro-text
        text-align: center
        font-size: 15px
        color: #3c363e

    .list-wrap
      overflow: scroll
      -webkit-overflow-scrolling: touch // 解决ISO下滑动卡顿
      .loading-more
        text-align: center
        padding: 10px 0
        .loading-image
          display: inline-block
          vertical-align: middle
          width: 26px
          height: 26px
          margin-right: 6px
          background-image: url('loading.gif')
          background-size: 26px 26px
          background-repeat: no-repeat
        .loading-text
          display: inline-block
          vertical-align: middle
          font-size: 15px
          color: #b6a6c6

      .loaded-text
        height: 40px
        line-height: 40px
        text-align: center
        color: #b6a6c6
        font-size: 15px

</style>



