import axios from 'axios'

const baseUrl = 'http://www.easy-mock.com/mock/59960692059b9c566dc54fc5/meitu'
const api = 'https://service.meitu.com/geo'

// 获取首页Home数据
export function getHomeData() {
  const url = `${baseUrl}/home/index`
  return axios.get(url).then((res) => {
    return Promise.resolve(res.data)
  }).catch((err) => {
    console.log(err)
  })
}

// 获取手机Phone数据
export function getPhoneData() {
  const url = `${baseUrl}/phone/lists`
  return axios.get(url).then((res) => {
    return Promise.resolve(res.data);
  }).catch((err) => {
    console.log(err)
  })
}

// 获取配件标题数据
export function getGoodsTitleData() {
  const url = `${baseUrl}/goods/titles`
  return axios.get(url).then((res) => {
    return Promise.resolve(res.data)
  }).catch((err) => {
    console.log(err)
  })
}

// 获取配件Goods数据
export function getGoodsListData(category, adaptation, page) {
  const url = `${baseUrl}/goods/lists${category}${adaptation}${page}`
  const data = Object.assign({}, {
    page: page,
    count: 10,
    category: category,
    adaptation: adaptation,
  })
  return axios.get(url, {
    params: data
  }).then((res) => {
    return Promise.resolve(res.data)
  }).catch((err) => {
    console.log(err)
  })
}

// 获取支持服务Supports数据
export function getSupportsData() {
  const url = `${baseUrl}/supports/lists`
  return axios.get(url).then((res) => {
    return Promise.resolve(res.data)
  }).catch((err) => {
    console.log(err)
  })
}

// 获取省份信息Province
export function getProvinceData() {
  const url = `${baseUrl}/shop/province`
  return axios.post(url).then((res) => {
    return Promise.resolve(res.data)
  }).catch((err) => {
    console.log(err)
  })
}

// 获取IP定位信息Location
export function getLocationData() {
  const url = `${api}/v1/location/ip`
  return axios.get(url).then((res) => {
    return Promise.resolve(res.data)
  }).catch((err) => {
    console.log(err)
  })
}

// 获取城市信息City
export function getCityData(id) {
  const url = `${baseUrl}/shop/city${id}`
  return axios({
    url: url,
    method: 'post',
    data: {
      province_id: id
    },
    transformRequest: [function (data) {
      for (let name in data) {
        data = encodeURIComponent(name) + '=' + encodeURIComponent(data[name])
      }
      return data
    }]
  })
}

// 获取当前城市售后列表Shop
export function getShopData(provinceId, cityId) {
  const url = `${baseUrl}/shop/lists${provinceId}${cityId}`
  const data = Object.assign({}, {
    province_id: provinceId,
    city_id: cityId,
    page: 1,
    count: 100
  })
  return axios.get(url, {
    params: data
  }).then((res) => {
    return Promise.resolve(res.data)
  }).catch((err) => {
    console.log(err)
  })
}

