// 获取配件Search数据
export function getSearchListData(type, category, adaptation, page) {
  const url = `${baseUrl}/search/${type}/lists${category}${adaptation}${page}`
  const data = Object.assign({}, {
    page: page,
    count: 10,
    category: category,
    adaptation: adaptation,
  })
  return axios.get(url, {
    params: data
  }).then((res) => {
    return Promise.resolve(res.data)
  }).catch((err) => {
    console.log(err)
  })
}

// 获取商品详情detail页面
export function getGoodDetail(goodId) {
  const url = `${baseUrl}/detail/${goodId}`
  return axios.get(url).then((res) => {
    return Promise.resolve(res.data)
  }).catch((err) => {
    console.log(err)
  })
}



