<template>
  <div id="app">
    <keep-alive :exclude="['goods', 'detail', 'search']">
      <router-view></router-view>
    </keep-alive>
    <waiting v-show="isWaiting"></waiting>
    <m-footer></m-footer>
  </div>
</template>

<script>
import MFooter from 'base/m-footer/m-footer'
import Waiting from 'base/waiting/waiting'

export default {
  computed: {
    isWaiting() {
      return this.$store.getters.isWaiting
    }
  },
  components: {
    Waiting,
    MFooter
  }
}
</script>

<style lang="stylus" scoped>
  @import "~common/stylus/variable"

    #app
      color: $color-theme
      .detail + .waiting + .m-footer,
      .user + .waiting + .m-footer,
      .cart + .waiting + .m-footer
        display: none
  
</style>



