<template>
  <div class="notice">
    <h3 class="notice-brief">关于四川地震灾区的物流限制公告 <span class="notice-show" @touchstart="show">查看公告</span></h3>
    <div class="notice-content" v-show="isShow" @touchmove.prevent>
      <div class="notice-close" @touchstart="hidden">
        <i class="close-button iconfont icon-close"></i>
      </div>
      <h4 class="notice-title">受8.8四川九寨沟地震灾害影响，手机发货（顺丰快递）做如下调整：</h4>
      <p class="notice-text">
        一、四川阿坝州下属九寨沟县、松潘县及四川绵阳市平武县（前往灾区必经区域）暂停收件服务。<br>
        二、全网暂停收取至四川阿坝州下属九寨沟县、松潘县及四川绵阳平武县（前往灾区必经区域）的所有快件；全网暂停收取至阿坝州其他网点区域的生鲜类快递，包括：时令水果、冷藏冷冻食品等对时效及温控要求高的物品。<br>
        三、四川阿坝州其他网点快件时效预计延误2个工作日。<br>
        &nbsp;&nbsp;&nbsp;&nbsp;以上调整开始日期为8月9日12点，恢复时间待通知。为给您造成的影响，深感抱歉并感谢您的谅解！
      </p>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    isShow: Boolean
  },
  methods: {
    show() {
      this.$emit('show')
    },
    hidden() {
      this.$emit('hidden')
    }
  }
}
</script>

<style lang="stylus" scoped>

  .notice
    .notice-brief
      position: relative
      height: 30px
      line-height: 30px
      text-align: center
      background: #555
      font-size: 15px
      color: #b5b5b5
      z-index: 3
      .notice-show
        font-size: 14px
        color: #efb336
    .notice-content
      position: absolute
      left: 0
      top: 0
      padding: 50px 20px 20px
      width: 90%
      box-sizing: border-box
      margin: 100px 5%
      border-radius: 4px
      z-index: 100
      background: #fff
      .notice-close
        position: absolute
        right: 0
        top: 0
        width: 50px
        height: 50px
        line-height: 50px
        text-align: center
        .close-button
          font-size: 18px
      .notice-title
        line-height: 24px
        font-size: 15px
        font-weight: bold
        color: #f55669
      .notice-text
        line-height: 24px
        font-size: 14px
        color: #666

</style>



