import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions'
import * as getters from './getters'
import state from './state'
import mutations from './mutations'
import createLogger from 'vuex/dist/logger'

Vue.use(Vuex)

const debug = process.env.NODE_ENV !== 'production' // 从环境变量判断当前的运行模式

export default new Vuex.Store({ // 导出store实例对象
    actions,
    mutations,
    getters,
    state,
    strict: debug, // 设置运行模式
    plugins: debug ? [createLogger()] : [] // 调试模式则加入日志模式
})


